'use client';

import { useEffect, useState } from 'react';
import { QRCodeSVG } from 'qrcode.react';

interface QRCodeShareProps {
  hashId: string;
  isOpen: boolean;
  onClose: () => void;
}

export default function QRCodeShare({ hashId, isOpen, onClose }: QRCodeShareProps) {
  const [isDesktop, setIsDesktop] = useState(false);
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);

    // „Éá„Çπ„ÇØ„Éà„ÉÉ„ÉóÂà§ÂÆö
    const checkDesktop = () => {
      setIsDesktop(window.innerWidth >= 768);
    };

    checkDesktop();
    window.addEventListener('resize', checkDesktop);

    return () => window.removeEventListener('resize', checkDesktop);
  }, []);

  // „Éû„Ç¶„É≥„ÉàÂâç„ÄÅ„É¢„Éê„Ç§„É´„ÄÅ„Åæ„Åü„ÅØÈñâ„Åò„Å¶„ÅÑ„ÇãÂ†¥Âêà„ÅØË°®Á§∫„Åó„Å™„ÅÑ
  if (!mounted || !isDesktop || !isOpen) {
    return null;
  }

  const botId = process.env.NEXT_PUBLIC_LINE_BOT_ID || '@YOUR_BOT_ID';
  // oaMessageÂΩ¢Âºè: Áõ¥Êé•„É°„ÉÉ„Çª„Éº„Ç∏„ÇíÈÄÅ‰ø°„Åô„Çã
  const message = `Codename:${hashId}`;
  const encodedMessage = encodeURIComponent(message);
  const qrUrl = `https://line.me/R/oaMessage/${botId}/?${encodedMessage}`;

  return (
    <div
      className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-80"
      onClick={onClose}
    >
      <div
        className="bg-gray-900 rounded-lg p-8 max-w-md w-full mx-4 shadow-2xl border border-gray-700"
        onClick={(e) => e.stopPropagation()}
      >
        {/* „Éò„ÉÉ„ÉÄ„Éº */}
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-xl font-bold text-white">„Çπ„Éû„Éõ„ÅßÈñã„Åè</h2>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-white transition"
            aria-label="Close"
          >
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>

        {/* QR„Ç≥„Éº„Éâ */}
        <div className="bg-white p-6 rounded-lg mb-6 flex justify-center">
          <QRCodeSVG
            value={qrUrl}
            size={256}
            level="H"
            includeMargin={true}
          />
        </div>

        {/* Ë™¨Êòé */}
        <div className="text-center space-y-4">
          <div className="text-gray-300 space-y-2">
            <p className="font-semibold">„Çπ„Éû„Éõ„ÅßQR„Ç≥„Éº„Éâ„ÇíË™≠„ÅøÂèñ„Çã„Å®</p>
            <ol className="text-sm text-gray-400 space-y-1 text-left pl-6">
              <li>1. LINE„Ç¢„Éó„É™„ÅåÈñã„Åç„Åæ„Åô</li>
              <li>2. Bot„Å´Ëá™ÂãïÁöÑ„Å´„É°„ÉÉ„Çª„Éº„Ç∏„ÅåÈÄÅ‰ø°„Åï„Çå„Åæ„Åô</li>
              <li>3. ÁîªÂÉè„Å®„Çπ„É©„Ç§„Éâ„Ç∑„Éß„Éº„É™„É≥„ÇØ„ÅåÂ±ä„Åç„Åæ„Åô</li>
              <li>4. „É™„É≥„ÇØ„Çí„Çø„ÉÉ„Éó„Åó„Å¶Â§ßÁîªÈù¢„ÅßË°®Á§∫</li>
            </ol>
          </div>

          {/* LINE BotÊÉÖÂ†± */}
          <div className="pt-4 border-t border-gray-700">
            <p className="text-xs text-gray-500">
              „ÄåBoom!„Éí„Éº„É≠„Éº!!„ÄçBot
            </p>
            <p className="text-xs text-gray-600 mt-1">
              {botId}
            </p>
          </div>
        </div>

        {/* Èñâ„Åò„Çã„Éú„Çø„É≥ */}
        <button
          onClick={onClose}
          className="mt-6 w-full bg-gray-700 hover:bg-gray-600 text-white py-3 rounded-lg transition"
        >
          Èñâ„Åò„Çã
        </button>
      </div>
    </div>
  );
}
